export default {
    filterProps(props, exclude) {
        let newProps = {};
        let excludeDict = {};
        for (let excl of exclude) {
            excludeDict[excl] = true;
        }
        Object.keys(props).forEach(prop => {
            if (!(prop in excludeDict)) {
                newProps[prop] = props[prop];
            }
        });
        return newProps;
    },

    passBrowserStateEvents(props) {
        let browserStateProps = {};
        props.onMouseEnter && (browserStateProps.onMouseEnter = props.onMouseEnter);
        props.onMouseLeave && (browserStateProps.onMouseLeave = props.onMouseLeave);
        props.onMouseDown && (browserStateProps.onMouseDown = props.onMouseDown);
        props.onMouseUp && (browserStateProps.onMouseUp = props.onMouseUp);
        props.onFocus && (browserStateProps.onFocus = props.onFocus);
        props.onBlur && (browserStateProps.onBlur = props.onBlur);

        return browserStateProps;
    },

    oppositeDirection(direction) {
        if (direction === 'column') {
            return 'row';
        } else if (direction === 'row') {
            return 'column';
        }
    },

    getSpacingStyle(key, spacing) {
        let spacingObj = {};
        if (typeof spacing === 'object') {
            if (spacing.all) {
                spacingObj[key] = spacing.all;
            }
            if (spacing.horizontal) {
                spacingObj[key + 'Left'] = spacing.horizontal;
                spacingObj[key + 'Right'] = spacing.horizontal;
            }
            if (spacing.vertical) {
                spacingObj[key + 'Top'] = spacing.vertical;
                spacingObj[key + 'Bottom'] = spacing.vertical;
            }
            if (spacing.top) {
                spacingObj[key + 'Top'] = spacing.top;
            }
            if (spacing.right) {
                spacingObj[key + 'Right'] = spacing.right;
            }
            if (spacing.bottom) {
                spacingObj[key + 'Bottom'] = spacing.bottom;
            }
            if (spacing.left) {
                spacingObj[key + 'Left'] = spacing.left;
            }
        } else {
            spacingObj[key] = spacing;
        }
        return spacingObj;
    },
}
