/**
 * Creates a merged style object. Falsey arguments are ignored.
 *
 * Use like:
 *  let style = mergeStyles(Styles.button, isSelected && {'color': 'black'});
 *
 * @returns {*} The merged style object
 */
export default function() {
    let truthyArgs = Array.from(arguments).filter(Boolean);
    return Object.assign({}, ...truthyArgs);
}
