import React, {Component} from "react";

import SilkUtil from '../SilkUtil';

import mergeStyles from '@silk/lib/mergeStyles';

const Styles = {
    common: {
        // opacity: 0.95,
        color: '#fff',
    },
    absoluteChungus: {
        fontSize: 72,
        fontWeight: 900,
    },
    chungus: {
        fontSize: 56,
        fontWeight: 900,
    },
    bigHeadline: {
        fontSize: 48,
        fontWeight: 400,
    },
    mediumHeadline: {
        fontSize: 32,
        fontWeight: 400,
    },
    headline: {
        fontSize: 24,
        fontWeight: 400,
    },
    title: {
        fontSize: 20,
        fontWeight: 600,
    },
    subhead: {
        fontSize: 16,
        fontWeight: 400,
    },
    body1: {
        fontSize: 14,
        fontWeight: 400,
    },
    body2: {
        fontSize: 14,
        fontWeight: 600,
    },
    caption: {
        fontSize: 12,
        fontWeight: 400,
    },
};

const EXCLUDE_PASSTHROUGH_PROPS = [
    'style',
    'textStyle',
    'lineHeight',
    'bold',
    'black',
    'secondary',
    'uppercase',
    'color',
    'solid',
    'centered'
];

export default class Text extends Component {
    static defaultProps = {
        textStyle: 'body1',
    };

    render() {
        let textStyle = Styles[this.props.textStyle];

        let styleFromProps = {};
        if (this.props.lineHeight) {
            styleFromProps.lineHeight = this.props.lineHeight;
        }
        if (this.props.bold) {
            styleFromProps.fontWeight = '600';
        }
        if (this.props.italic) {
            styleFromProps.fontStyle = 'italic';
        }
        if (this.props.black) {
            styleFromProps.fontWeight = '900';
        }
        if (this.props.solid) {
            styleFromProps.opacity = 1.0;
        }
        if (this.props.sub) {
            styleFromProps.opacity = 0.8;
        }
        if (this.props.secondary) {
            styleFromProps.opacity = 0.54;
        }
        if (this.props.disabled) {
            styleFromProps.opacity = 0.38;
        }
        if (this.props.uppercase) {
            styleFromProps.textTransform = 'uppercase';
        }
        if (this.props.color) {
            styleFromProps.color = this.props.color;
        }
        if (this.props.centered) {
            styleFromProps.textAlign = 'center';
        }

        return (
            <span style={mergeStyles(Styles.common, textStyle, styleFromProps, this.props.style)}
                  {...SilkUtil.filterProps(this.props, EXCLUDE_PASSTHROUGH_PROPS)}>
                {this.props.children}
            </span>
        );
    }
}
