import React from 'react';

import db from '@dhcsgo/lib/db';

import { useRouteMatch } from 'react-router-dom';

import Box from '@silk/Box';
import Text from '@silk/Text';
import Navbar from '@silk/Navbar';

const Styles = {
    root: {
        backgroundColor: '#222',
    },
    content: {
        paddingLeft: 32,
        paddingRight: 32,
        width: '100%',
        maxWidth: 1000,
    },
};

export default function TeamScene() {
    const match = useRouteMatch();
    const { params } = match;
    const team = db.teams[params.id];
    return (
        <Box flex={1} style={Styles.root}>
            <Navbar />
            <Box flex={1} alignItems="center" style={{paddingLeft: 32, paddingRight: 32, paddingTop: 32}}>
                <Box style={Styles.content}>
                    <Text textStyle="chungus">{team.name}</Text>
                    <Text textStyle="headline" color="#ffc40a">Team Roster</Text>
                    <Box direction="row" justifyContent="center" style={{marginTop: 32}}>
                        <Text sub>Check back later for team rosters and statistics.</Text>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}
