import React, { Component } from 'react';

import SilkUtil from '@silk/SilkUtil';

import mergeStyles from '@silk/lib/mergeStyles';

const EXCLUDE_PASSTHROUGH_PROPS = [
    'direction',
    'justifyContent',
    'alignItems',
    'flex',
    'backgroundColor',
    'borderRadius',
    'margin',
    'padding',
    'style',
];

export default class Box extends Component {
    static defaultProps = {
        direction: 'column',
    };

    get divRef() {
        return this.rootRef;
    }

    constructor(props) {
        super(props);

        this.rootRef = null;
    }

    render() {
        let style = {
            display: 'flex',
        };
        if (this.props.direction) {
            style.flexDirection = this.props.direction;
        }
        if (this.props.justifyContent) {
            style.justifyContent = this.props.justifyContent;
        }
        if (this.props.alignItems) {
            style.alignItems = this.props.alignItems;
        }
        if (this.props.flex) {
            let flexProp = this.props.flex;
            if (typeof flexProp === 'object') {
                (flexProp.grow !== undefined) && (style.flexGrow = flexProp.grow);
                (flexProp.shrink !== undefined) && (style.flexShrink = flexProp.shrink);
                (flexProp.basis !== undefined) && (style.flexBasis = flexProp.basis);
            } else {
                style.flex = flexProp;
            }
        }
        if (this.props.backgroundColor) {
            style.backgroundColor = this.props.backgroundColor;
        }
        if (this.props.borderRadius) {
            style.borderRadius = this.props.borderRadius;
        }
        if (this.props.margin) {
            style = mergeStyles(style, SilkUtil.getSpacingStyle('margin', this.props.margin));
        }
        if (this.props.padding) {
            style = mergeStyles(style, SilkUtil.getSpacingStyle('padding', this.props.padding));
        }
        if (this.props.height || this.props.height === 0) {
            style.height = this.props.height;
        }
        style = mergeStyles(style, this.props.style);
        return (
            <div ref={ref => { this.rootRef = ref }} style={style} {...SilkUtil.filterProps(this.props, EXCLUDE_PASSTHROUGH_PROPS)}>
                {this.props.children}
            </div>
        );
    }
}
